body {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 20px;
  font-family: "Arial", sans-serif;
  color: #ece5e5;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.header {
  position: fixed;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  width: 100%;
  min-height: 15vh;
}
.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  background-image: url(../img/background-main.jpg);
  background-position: 50% 50%;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}
.main-nav {
  position: relative;
}
.main-nav__list {
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
  margin-right: 35px;
  list-style: none;
  padding: 0;
}
.main-nav__item {
  margin: 7px auto;
  text-align: center;
}
.main-nav__link {
  padding: 10px 30px;
  color: #ece5e5;
  line-height: 1em;
  letter-spacing: 1px;
  text-decoration: none;
}
.main-nav__link:hover {
  color: #4deeea;
  transition: color 1000ms ease;
}
.main-nav__link:active {
  color: #4a8399;
  opacity: 0.4;
  transition: opacity 600ms ease;
}
.main-nav__toggle {
  display: none;
}
.logo {
  position: relative;
  z-index: 20000;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-top: 20px;
}
.logo__img {
  position: absolute;
  width: 50%;
}
.logo__text {
  margin-left: 30%;
  color: #ece5e5;
}
@media (max-width: 850px) {
  .logo {
    padding-top: 5px;
  }
}
.carousel-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 60vh;
  margin-top: 40px;
  background-color: #031219;
}
.carousel-section__inner {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  width: 85%;
  height: 70%;
  border: 1px solid #bbe2f1;
  border-radius: 11px;
  background-color: rgba(255, 255, 255, 0.1);
}
.carousel-section__inner::after {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  z-index: 1000;
  display: block;
  width: 2px;
  height: 100%;
  background-color: #ece5e5;
}
.carousel-section__btn {
  margin-top: 20px;
}
.carousel {
  width: 90%;
}
.card {
  position: relative;
  flex-direction: column;
  justify-content: baseline;
  align-items: space-between;
  width: 100%;
  overflow: hidden;
}
.card__title {
  margin: 0;
  margin-top: 5px;
  font-size: 18px;
  color: #ece5e5;
  text-align: center;
}
.card__link {
  position: absolute;
  top: -10%;
  z-index: 100;
  width: 100%;
  height: 110%;
  background-color: transparent;
  transition: background-color 800ms ease;
}
.card__link:hover {
  background-color: rgba(74, 131, 153, 0.2);
  transition: background-color 800ms ease;
}
@media (max-width: 850px) {
  .card__title {
    font-size: 14px;
  }
}
.btn {
  width: 250px;
  padding: 9px 15px;
  font-size: 22px;
  font-weight: 700;
  text-align: center;
  color: #ece5e5;
  border: 2px solid #4a8399;
  background-color: transparent;
  transition: background-color 800ms ease;
}
.btn:hover {
  background-color: rgba(74, 131, 153, 0.5);
  transition: background-color 800ms ease;
}
.btn:active {
  background-color: rgba(74, 131, 153, 0.2);
  transition: background-color 800ms ease;
}
